@font-face {
  font-family: 'GurbaniAkhar';
  font-style: normal;
  font-weight: 400;
  src: local('OpenGurbaniAkhar-Regular'),
       url('fonts/OpenGurbaniAkhar-Regular.otf') format('opentype')
}

@font-face {
  font-family: 'AnmolUniBani';
  src: local('AnmolUniBani'),
       url('fonts/AnmolUniBani.ttf') format('truetype')
}

@font-face {
  font-family: 'OpenAnmolUni';
  src: local('OpenAnmolUni'),
       url('fonts/OpenAnmolUni-Bold.woff') format('woff')
}

@font-face {
  font-family: 'OpenSatlujUni';
  src: local('OpenSatlujUni'),
       url('fonts/OpenSatlujUni-Regular.otf') format('opentype')
}

.component-conversion-result-main-gurbani {
  padding: 5px;
  background-color: #243447;
  color: #ffffff;
  font-size: 25px;
  font-family: "OpenSatlujUni";
}

.component-conversion-result-main-hindi-gurbani {
  padding: 5px;
  background-color: #243447;
  color: #ffffff;
  font-size: 20px;
  font-family: "OpenSatlujUni";
}

.component-conversion-result-main {
  padding: 5px;
  background-color: #243447;
  color: #ffffff;
  font-size: 15px;
  font-family: 'Noto Sans Gurmukhi', sans-serif;
}

.component-conversion-result-main-anmol {
  padding: 5px;
  background-color: #243447;
  color: #ffffff;
  font-size: 15px;
  font-family: 'OpenAnmolUni', sans-serif;
}

.component-conversion-result-main-hindi {
  padding: 5px;
  background-color: #243447;
  color: #ffffff;
  font-size: 15px;
  font-family: 'Noto Sans Devanagari', sans-serif;
}

.component-conversion-result-main-urdu {
  padding: 5px;
  padding-top: 50px;
  background-color: #243447;
  color: #ffffff;
  font-size: 45px;
  font-family: 'Noto Nastaliq Urdu', sans-serif;
}

.component-conversion-result-english {
  padding: 5px;
  background-color: #243447;
  color: #ffffff;
  font-size: 15px;
  /* font-family: "GurbaniAkhar"; */
  font-family: 'Noto Sans', sans-serif;
}
